:root {
  --header-bg: #262626;
  --hero-bg: #f4f4f4;
  --blue-color: #155dfc;
  --input-color: #f5f5f5;
  --input-tcolor: #242424;
  --bluew-color: #5b5fc7;
  --nunito: "Nunito";
  --noto-sans: "Noto Sans";
  --white-color: #fff;
  --black-color: #000;
  --hover-color: #e1712b;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  font-family: var(--font-family);
  border: 0;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  color: var(--white-color);
  line-height: 1;
  font-family: var(--nunito);
  font-weight: 400;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.header {
  background-color: var(--header-bg);
  justify-content: flex-start;
  align-items: center;
  padding: 25px 30px 25px 10px;
  display: flex;
}

.header-logo {
  width: 55px;
  height: auto;
  margin-right: auto;
}

.header-nav {
  gap: 35px;
  display: flex;
}

.header-link {
  color: var(--text-color);
  transition: color .4s;
  position: relative;
}

.header-link:after {
  content: "";
  background-color: var(--hover-color);
  width: 0;
  height: 2px;
  transition: width .3s, left .3s;
  position: absolute;
  bottom: -5px;
  left: 50%;
}

.header-link:hover {
  color: var(--hover-color);
}

.header-link:hover:after {
  width: 100%;
  left: 0;
}

.hero {
  background-image: url("hero-background.d52e3958.png");
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.hero-box {
  font-family: var(--noto-sans);
  background: #18181bb3;
  border-radius: 7px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 35px 25px;
  display: flex;
}

.hero-title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 70px;
  font-weight: 500;
}

.hero-subtitle {
  font-size: 20px;
}

.intro {
  background-color: var(--hero-bg);
  color: var(--black-color);
  font-family: var(--noto-sans);
  text-align: center;
  padding-top: 40px;
  padding-bottom: 110px;
  font-weight: 500;
}

.intro-title {
  margin-bottom: 40px;
  font-size: 40px;
}

.intro-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 35px;
  display: flex;
}

.intro-card {
  background-color: var(--white-color);
  border-radius: 7px;
  justify-content: flex-start;
  align-items: center;
  gap: 30px;
  width: 800px;
  padding: 15px 10px;
  transition: transform .5s cubic-bezier(.175, .885, .32, 1.275);
  display: flex;
  box-shadow: 0 14px 28px #00000040, 0 10px 10px #00000038;
}

.intro-card:hover {
  transform: scale(1.05);
}

.icard-image {
  border-radius: 20px;
  width: 150px;
  height: 150px;
}

.icard-box {
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  gap: 7px;
  display: flex;
}

.icard-subtitle {
  color: var(--blue-color);
  margin-bottom: 20px;
}

.footer {
  background-color: var(--header-bg);
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: 25px;
  padding-bottom: 25px;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
}

.destination {
  text-align: center;
  color: var(--black-color);
  background-color: var(--hero-bg);
  padding-top: 35px;
  padding-bottom: 95px;
}

.destination-title {
  font-size: 45px;
  font-family: var(--noto-sans);
  margin-bottom: 35px;
  font-weight: 600;
}

.destination-list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 35px;
  display: flex;
}

.ditem {
  background-color: var(--white-color);
  width: 500px;
  height: 400px;
  transition-property: transform box-shadow;
  border-radius: 7px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 25px;
  padding-top: 15px;
  padding-bottom: 15px;
  transition-duration: .3s;
  display: flex;
  box-shadow: 0 3px 8px #0000003d;
}

.ditem:hover {
  transform: scale(1.05);
  box-shadow: 0 10px 20px #00000030, 0 6px 6px #0000003b;
}

.ditem-image {
  border-radius: 7px;
  width: 450px;
}

.ditem-title {
  color: var(--blue-color);
  font-size: 25px;
}

.contacts {
  color: var(--black-color);
  background-color: var(--hero-bg);
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 25px;
  padding-top: 35px;
  padding-bottom: 65px;
  display: flex;
}

.contacts-title {
  font-family: var(--noto-sans);
  font-size: 35px;
  font-weight: 600;
}

.contacts-form {
  background-color: var(--white-color);
  border-radius: 7px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 25px;
  width: 350px;
  height: 350px;
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
  box-shadow: 0 7px 29px #64646f33;
}

.contacts-input {
  background-color: var(--input-color);
  max-width: 190px;
  color: var(--input-tcolor);
  border: none;
  border-radius: 4px;
  outline: none;
  min-height: 40px;
  padding: .15rem .5rem;
  line-height: 1.15;
  box-shadow: 0 10px 20px -18px;
}

.contacts-input:focus {
  border-bottom: 2px solid var(--bluew-color);
  border-radius: 4px 4px 2px 2px;
}

.contacts-input:hover {
  outline: 1px solid #d3d3d3;
}

#contacts-textarea {
  background-color: var(--input-color);
  color: var(--input-tcolor);
  border: none;
  border-radius: 4px;
  outline: none;
  min-width: 50px;
  min-height: 100px;
  padding: .15rem .5rem;
  line-height: 1.15;
  box-shadow: 0 10px 20px -18px;
}

#contacts-textarea:focus {
  border-bottom: 2px solid var(--bluew-color);
  border-radius: 4px 4px 2px 2px;
}

#contacts-textarea:hover {
  outline: 1px solid #d3d3d3;
}

.contacts-button {
  --clr-font-main: #333;
  --btn-bg-1: #61daff;
  --btn-bg-2: #1f75ff;
  --btn-bg-color: #fff;
  --radii: .5em;
  cursor: pointer;
  min-width: 120px;
  min-height: 44px;
  font-size: var(--size, 1rem);
  background-size: 280%;
  background-image: linear-gradient(325deg, var(--btn-bg-2) 0%, var(--btn-bg-1) 55%, var(--btn-bg-2) 90%);
  border-radius: var(--radii);
  color: var(--btn-bg-color);
  border: none;
  padding: .9em 1.4em;
  font-weight: 500;
  transition: all .8s;
  box-shadow: 0 0 20px #47b8ff80, 0 5px 5px -1px #3a7de940, inset 4px 4px 8px #afe6ff80, inset -4px -4px 8px #135fd859;
}

.contacts-button:hover {
  background-position: 100% 0;
}

.contacts-button:is(:focus, :focus-visible, :active) {
  box-shadow: 0 0 0 3px var(--btn-bg-color), 0 0 0 6px var(--btn-bg-2);
  outline: none;
}

@media (prefers-reduced-motion: reduce) {
  .contacts-button {
    transition: all linear;
  }
}

.modal {
  z-index: 1000;
  background: #0006;
  width: 100%;
  height: 100%;
  transition: display .5s cubic-bezier(.17, .67, .83, .67);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  text-align: center;
  background: #fff;
  border-radius: 20px;
  width: 420px;
  max-width: 90%;
  padding: 40px 45px;
  font-size: 18px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 12px 35px #00000040;
}

.modal-content h2 {
  margin-bottom: 15px;
}

.close {
  float: right;
  cursor: pointer;
  font-size: 24px;
}

@media (width <= 1024px) {
  .header {
    padding-left: 10px;
    padding-right: 20px;
  }

  .header-nav {
    background-color: var(--header-bg);
    border-radius: 7px;
    gap: 15px;
    padding: 10px 20px;
    display: flex;
    top: 25px;
    right: 20px;
  }

  .hero-title {
    font-size: 55px;
  }

  .intro-title {
    font-size: 32px;
  }

  .intro-card {
    gap: 25px;
    width: 650px;
    padding-left: 15px;
    padding-right: 15px;
  }

  .icard-image {
    width: 130px;
    height: 130px;
  }

  .destination-title {
    font-size: 38px;
  }

  .ditem {
    width: 420px;
    height: 360px;
  }

  .ditem-image {
    width: 380px;
  }

  .contacts-form {
    width: 320px;
  }
}

@media (width <= 768px) {
  .header {
    justify-content: space-between;
    padding-right: 15px;
  }

  .hero-title {
    font-size: 42px;
  }

  .hero-subtitle {
    font-size: 18px;
  }

  .intro-title {
    font-size: 28px;
  }

  .intro-card {
    text-align: center;
    flex-direction: column;
    gap: 20px;
    width: 90%;
  }

  .icard-image {
    width: 120px;
    height: 120px;
  }

  .destination-title {
    font-size: 32px;
  }

  .ditem {
    width: 90%;
    height: auto;
    padding-bottom: 25px;
  }

  .ditem-image {
    width: 90%;
  }

  .contacts-form {
    width: 280px;
    height: auto;
    padding-top: 20px;
    padding-bottom: 20px;
  }

  .contacts-input {
    max-width: 160px;
  }
}

@media (width <= 480px) {
  .hero-title {
    font-size: 32px;
  }

  .hero-box {
    padding-left: 15px;
    padding-right: 15px;
  }

  .intro-title {
    font-size: 24px;
  }

  .icard-image {
    width: 100px;
    height: 100px;
  }

  .ditem-title {
    font-size: 20px;
  }

  .contacts-title {
    font-size: 28px;
  }

  .modal-content {
    width: 300px;
    padding: 30px;
  }
}
